<template>
    <div>
        <span></span>
        <h3>防抖函数 debounce</h3>
        <p>多次触发事件，但只需要执行一次，可用于防止多次提交重复表单</p>
        <p>原理是利用定时器的设置和清空，在下方例子中，resize 窗口后 250ms 触发想要执行的事件，并设置定时器，如果在这段时间里频繁改变窗口大小，则清空现有定时器并创建新的定时器。虽然用户有多次 resize 操作，但是最后要执行的函数只会在最后一次中执行。</p>
        <p>代码参考<a href="https://rockjins.js.org/2017/02/21/2017-02-21-debounce-function/">Rockjins Blog</a></p>
        <div class="code">
            <code>
            function debounce(fn, interval, immediate) {
                //fn为要执行的函数
                //interval为等待的时间
                //immediate判断是否立即执行
                var timeout;  //定时器
                return function() { //返回一个闭包
                    var context = this, args = arguments; //先把变量缓存
                    var later = function() {  //把稍后要执行的代码封装起来
                        timeout = null; //成功调用后清除定时器
                        if(!immediate) fn.apply(context, args); //不立即执行时才可以调用
                    };
                    var callNow = immediate && !timeout;  //判断是否立即调用，并且如果定时器存在，则不立即调用
                    clearTimeout(timeout);  //不管什么情况，先清除定时器，这是最稳妥的
                    timeout = setTimeout(later, interval);  //延迟执行
                    if(callNow) fn.apply(context, args);  //如果是第一次触发，并且immediate为true，则立即执行
                };
            };
            //使用
            var myEfficientFn = debounce(function() {
            //你要做的事
            }, 250);
            window.addEventListener('resize', myEfficientFn);
        </code>
        </div>
    </div>
</template>

