<script>


export default {
    name: 'Notes',
    data() {
        return  {
            show: true
        }
    },
    methods: {
        hide: function () {
            if (this.$route.path.split('/').length > 2) {
                this.show = false
            } else {
                this.show = true
            }
        }
    },
    mounted(){
        this.hide()
    },
    watch: {
        '$route'() {
            this.hide()
        }
    }
}
</script>

