<template>
    <div>
        <div class="part">
            <h4>1s 后验证用户输入号码</h4>
            <form action="">
                <label for="phoneNumber">Phone Number</label>
                <div>
                    <input type="text" name="phoneNumber" id="phoneNumber" v-model="phone" maxlength="11" v-bind:class="{ error_number:failed }">
                    <p v-show="failed">Wrong number, please input again</p>
                    <p v-show="success">Number is ok</p>
                </div>
            </form>
        </div>
        <div class="part">
            <h4>秒杀倒计时</h4>
            <div>
                <button id="buy" v-bind:class="{ not_allow_buy: !start || buy }" @click="buyIt">BUY</button>
                <p v-show="buy">成功参加活动</p>
                <p v-show="end">距离活动<span style="color:green">开始</span>还有 {{ timeToStart/1000 }} 秒 </p>
                <p v-show="start">距离活动<span style="color:orange">结束</span>还有 {{ timeToEnd/1000 }} 秒 </p>
                <p v-show="(!start) && (!end)">活动已经结束</p>
            </div>
        </div>
    </div>
</template>

<script>
import { setTimeout, clearTimeout, setInterval, clearInterval } from 'timers';
export default {
    data() {
        return {
            phone: undefined,
            failed: false,
            success: false,
            
            now: Date.now(),
            startTime: 0,
            endTime: 0,
            start: false,
            end: true,
            timeToStart: 0,
            timeToEnd: 0,
            buy: false
        }
    }, 
    beforeCreate() {
    },
    created() {
        this.startTime = this.now + 1000 * 10
        this.endTime = this.now + 1000 * 10
        this.timeToStart = this.startTime - this.now
        this.timeToEnd = this.endTime - this.now
        this.startInterval = setInterval(() => {
            this.timeToStart -= 1000
        }, 1000)
        
    },
    beforeMount() {
    },
    mounted() {
    },
    updated() {
        if (this.timeToStart == 0) {
            this.timeToStart = -1
            clearInterval(this.startInterval)
            this.start = true
            this.end = false
            this.endInterval = setInterval(() => {
                this.timeToEnd -= 1000
            }, 1000)
        }

        if (this.timeToEnd == 0) {
            clearInterval(this.endInterval)
            this.start = false
            this.end = false
        }
    },
    methods: {
        buyIt: function () {
            if (this.start && (!this.buy)) {
                alert('Successful Post')
                this.buy = true
            }
        }
    },
    watch: {
        phone: (function () {
            var phoneTimeout;
            return function () {
                clearTimeout(phoneTimeout)
                phoneTimeout = setTimeout(() => {
                    if (!/^1[34678]\d{9}$/.test(this.phone)) {
                        this.failed = true
                    } else {
                        this.failed = false
                        this.success = true
                    }
                }, 1000);
            }
        })()
    }
}
</script>

