    padding: 5px 10px;
    font-size: 20px;
    outline: none;
}

.part {
    margin-bottom: 100px; 
}

.error_number {
    border: 1px solid red;
}

#buy {
    width: 200px;
    height: 40px;
    border: 1px solid gray;
    border-radius: 10px;
    font-size: 20px;
}

.not_allow_buy {
    cursor: not-allowed;
    color: gray;
}


</style>
