<template>
    <div style="position: relative;">
        <div id="animation" class="animation_layout">

        <div class="block block-A center">
            <h4>CSS3 旋转效果</h4>
            <div class="rotate" ></div>
        </div>

        <div class="block block-A center">
            <h4>缩放效果</h4>
            <div class="heart"></div>
        </div>

        <div class="block block-A center">
             <h4>球体掉落</h4>
            <div class="ball" ></div>
        </div>

        <div class="block block-B" style="text-align: none;">
                <h4 class="center">3d 转换</h4>
                <!-- <p>分为三层，stage、inner 和图片。stage 设置透视，通过控制 inner 旋转实现图片切换，图片通过绝对定位聚于一点，（360/图片张数）来设置旋转角度之间的差值。最后用 translateZ 实现图片之间的分离，translateZ = (width/2) * tan(img_num/360) + img_margin</p> -->
                <div class="stage">
                    <div class="inner" style="transform:rotateY(0deg);" @click="changePict">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                        <img src="" alt="" class="img">
                    </div>
                </div>
        </div>

        <div class="block block-A center">
            <h4>加载</h4>
            <div class="loading">
                    <span class="load-part load-one"></span>
                    <span class="load-part load-two"></span>
                    <span class="load-part load-three"></span>
                    <span class="load-part load-four"></span>
                    <span class="load-part load-five"></span>
                </div>
        </div>

        <div class="block block-C ">
            <h4 class="center">canvas</h4>
            <canvas id="canvas"  width="780" height="300"></canvas>
        </div>
        
    </div>
    </div>
</template>

<script>

export default {
    name: 'Animation',
    data() {
        return { 
            canvas_width: window.innerWidth
        }
    },
    mounted(){
        function getRandomInt (min, max) {
            return Math.floor(Math.random() * (max - min) + min)
        }

        function Ball() {}

        let canvas_width = document.getElementById('canvas').width
        if (this.canvas_width < 760 && this.canvas_width > 400) {
            document.getElementById('canvas').width = 380
            this.canvas_width = 380
        } else if (this.canvas_width < 400){
            document.getElementById('canvas').width = 320
            this.canvas_width = 320
        }
        
        Ball.prototype = {
            x: 100,
            y: 100,
            radius: 10,
            vx: 3,
            vy: 5,
            color: '#76b39d',
            draw: function () {
                ctx.beginPath();
                ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2, true);
                ctx.closePath();
                ctx.fillStyle = this.color;
                ctx.fill();
            },
            run: function () {
                this.x += this.vx
                this.y += this.vy
            }
        }

        let canvas = document.getElementById('canvas')
        let ctx = canvas.getContext('2d')
        let colorList = ['#ff9090', '#ea8a8a', '#ffe5ab', 
                        '#feb062','#89a4c7', '#f2c6b4', 
                        '#bfd8d5', '#fed9ca', '#dbe9b7']
        let ballList = []
        let ballNumber = 10

        for (let i = 0; i < ballNumber; i++) {
            let b = new Ball()
            b.radius = getRandomInt(10,15)
            b.x = getRandomInt(1, canvas_width)
            b.y = getRandomInt(1, 50)
            b.vx = getRandomInt(1, 5)
            b.vy = getRandomInt(1, 5)
            b.color = colorList[getRandomInt(0, colorList.length)]
            b.draw()
            ballList.push(b)
        }

        function ballRun() {
            ctx.clearRect(0,0, canvas.width, canvas.height)
            let l = ballList.length
            let h = canvas.height
            let w = canvas.width
            for (let i = 0; i < l; i++) {
                let cur = ballList[i]
                if(cur.y > h - cur.radius || cur.y < cur.radius){
                    cur.vy *= -1
                }
                if(cur.x > w - cur.radius || cur.x < cur.radius){
                    cur.vx *= -1 
                }         
                
                cur.run()
                cur.draw()
            }
            window.requestAnimationFrame(ballRun)
        }
        ballRun()
        // canvas ball run end

        // 3d picture auto change
        this.pictInterval = setInterval(() => {
            this.changePict()
        }, 2000);
    },
    methods: {
        changePict: function () {
            let inner = document.getElementsByClassName('inner')[0];
            let reg=/\d{1,}/;
            // alert(reg)
            let num = inner.style.transform.toString().match(reg)[0];
            num = parseInt(num) + 60;
            inner.style.transform = 'rotateY(' + num + 'deg)';
        }

    },
    beforeDestroy: function () {
        clearInterval(this.pictInterval)
    }
}
</script>

<style scoped>

